	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"There is a good amount of excitement about the new version of Java. The big evolution of course is the lambda expressions. In this presentation we will dive into the language features in Java 8, take a look at some of their nuances, and look at ways to put them to good use. In addition to looking at lambdas we will also dive into the Streams capabilities and also some of the features in Java 8 that make all these possible.","meta":{"duration":"PT1H25M27S"},"presenter":{"name":"Venkat Subramaniam"},"source":{"id":"j9nj5dTo54Q","provider":"youtube"},"slug":"java-8-language-capabilities-whats-in-it-for-you","tag":["Spring Framework","Java"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/j9nj5dTo54Q/default.jpg","height":90,"width":120}},"title":"Java 8 Language Capabilities, What's in it for you?"}}]}},"pathContext":{"speaker":"Venkat Subramaniam"}}

